@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
header {
    height: 100vh;
    overflow: hidden;
    padding-top: 7rem;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 8.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}


.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials,
    .scroll__down{
        display: none;
    }
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f2833;
    --color-bg-variant: #2c2c6c;
    --color-primary: #00d4ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);
    --color-other: #0f243d;

    --color-bg2: #1f2833;
    --color-bg2-variant: #0b0c10;
    --color-primary2: #66FCF1;
    --color-primary2-variant: #45a29e;
    --color-light-variant: #c5c6c7;

    --transition: all 400ms ease;

    --container-width-l: 75%;
    --container-width-m: 86%;
    --container-width-s: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: #1f2833;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    line-height: 1.7;
}

body {
    background-color: #0b0c10;
        opacity: 1;
        background-image: radial-gradient(#1f2833 0.8500000000000001px, #0b0c10 0.8500000000000001px);
        background-size: 17px 17px;
}

/* other styles */

.container {
    width: 75%;
    width: var(--container-width-l);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2,
section > h5 {
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

section > h2 {
    color: #00d4ff;
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: rgba(255, 255, 255, 0.6);
    color: var(--color-light);
}

a {
    color: #00d4ff;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #fff;
    color: var(--color-white);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #00d4ff;
    color: var(--color-primary);
    padding: 0.35rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #00d4ff;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
    height: 40px;
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #1f2833;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #00d4ff;
    background: var(--color-primary);
    color: #1f2833;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-m);
    }

    section {
        margin-top: 6rem;
    }
}

@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-s);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}

ul {
    list-style: none;
    display: inline;
}

li {
    display: inline;
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    display: grid;
    place-items: center;

}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transition: var(--transition);
}
/* edit about me image hover pls make it from greyscale to RGB */

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg2);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.profile__img {
    opacity: 0.5;
    border-radius: 10px;
    transition: var(--transition);
}

.profile__img:hover {
    opacity: 1;
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}


@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}

.contact__container {
    display: grid;
    grid-template-columns: repeat(2, -webkit-max-content);
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    grid-column-gap: 6rem;
    -webkit-column-gap: 6rem;
            column-gap: 6rem;
    padding-bottom: 3rem;
}

.contact-title {
    text-align: center;
    margin-bottom: 1.5rem;
}

.contact__info {
    display: grid;
    grid-row-gap: 1rem;
    row-gap: 1rem;
    grid-template-columns: 300px;
}

.contact__card {
    background-color: var(--color-bg);
    border: 1px solid rgba(0,0, 0, 0.3);
    border-radius: 0.75rem;
    text-align: center;
    transition: var(--transition);
}

.contact-icon {
    font-size: 2rem;
    color: var(--color-primary);
    margin-bottom: 0.25rem;
    margin-top: 0.75rem;
}

.contact__card-details {
    display: block;
    margin-bottom: 0.75rem;
}

.anchor {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    grid-column-gap: 0.3rem;
    -webkit-column-gap: 0.3rem;
            column-gap: 0.3rem;
}


.anim-icon {
    transition: var(--transition);
}

.anchor:hover .anim-icon {
    -webkit-transform: translate(0.25rem);
            transform: translate(0.25rem);
}

.contact__form {
    width: 360px;
}

.contact__form-div{
    position: relative;
    margin-bottom: 2rem;
    height: 4rem;
}

.contact__form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0, 212, 255, 0.6);
    outline: none;
    border-radius: 0.75rem;
    color: var(--color-white);
    padding: 1.5rem;
    z-index: 1;
    background-color: transparent; 
}

.contact__form-input::-webkit-input-placeholder {
    color: var(--color-light);
}

.contact__form-input::placeholder {
    color: var(--color-light);
}

.contact__form-tag {
    position: absolute;
    top: -1.15rem;
    left: 1.25rem;
    padding:0.25rem;
    z-index: 10;
    color: var(--color-white);
    background-color: var(--color-bg2-variant);
}

.contact__form-area {
    height: 11rem;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border: 2px solid rgba(0, 212, 255, 0.6);
    outline: none;
    border-radius: 0.75rem;
    padding: 1.5rem;
    z-index: 1;
    background-color: transparent;
    color: var(--color-white);
    font-family: sans-serif;
}

.contact__form-area {
    resize: none;
}

button {
    padding: 1rem 1.75rem;
    border-radius: 0.75rem;
    background-color: transparent;
    margin-top: 6rem;
}

button:hover {
    color: black;
    background-color: var(--color-primary);
    cursor: pointer;
}

.contact__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}


@media screen and (max-width: 1024px) {
    .contact__container {
        grid-column-gap: 3rem;
        -webkit-column-gap: 3rem;
                column-gap: 3rem;
    }
}

@media screen and (max-width: 600px) {
    .contact__container {
        grid-template-columns: 1fr;
        grid-row-gap: 3rem;
        row-gap: 3rem;
    }


    .contact__info {
        justify-content: center;
    }

    .contact__form {
        margin: 0 auto;

    }
}


@media screen and (max-width: 576px) {
    .contact__info {
        grid-template-columns: 1fr;
    }

    .contact__form {
        width: 100%;
    }
}
.services__container {
    display: grid;
    grid-template-columns: repeat(3, 370px);
    justify-content: center;
    grid-column-gap: 1.4rem;
    -webkit-column-gap: 1.4rem;
            column-gap: 1.4rem;
    max-width: 968px;
    margin-left: auto;
    margin-right: auto;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
}



.services__content {
    position: relative;
    border: 2px solid rgba(255, 255, 255, 0.02);
    background-color: rgba(15, 36, 61, 0.3);
    padding: 6rem 0 2rem 2.5rem;
    border-radius: 5px;
    transition: var(--transition);
}


.services__icon {
    display: block;
    font-size: 2rem;
    color: var(--color-primary);
    margin-bottom: 6rem;
}


.services__btn {
    margin-top: 1.5rem;
    display: inline-flex;
    align-items: center;
    grid-column-gap: 0.3rem;
    -webkit-column-gap: 0.3rem;
            column-gap: 0.3rem;
}

.arrow {
    font-size: 1rem;
    transition: var(--transition);
}

.arrow:hover {
    -webkit-transform: translateX(0.25rem);
            transform: translateX(0.25rem);
}


.services__modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    opacity: 0;
    visibility: hidden;
    transition: var(--transition);
}

.services__modal-content {
    width: 500px;
    position: relative;
    background-color: rgba(15, 36, 61, 1);
    padding: 4.5rem 2.5rem 2.5rem;
    border-radius: 1.5rem;
}

.close-icon {
    position: absolute;
    top: 1rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color: rgb(77, 181, 255);
    cursor: pointer;
}

.services__modal-content > h3,
.services__modal-content > p{
    text-align: center;
}

.services__modal-content h3 {
    padding-bottom: .75rem;
    font-size: 1.7rem;
    margin-bottom: 2.5rem;
    font-weight: 700;
}

.services__content > p{
    padding: 1rem 3.5rem;
    color: var(--color-light-variant);
    font-weight: 300;
    margin-bottom: 1rem;
    font-size: 1.2rem;
}

.services__modal-services {
    grid-row-gap: 0.75rem;
    row-gap: 0.75rem;
}

.services__modal-services {
    display: flex;
    align-items: center;
    grid-column-gap: 0.5rem;
    -webkit-column-gap: 0.5rem;
            column-gap: 0.5rem;
}

.check-icon {
    color: var(--color-primary);
    font-size: 1.1rem;
    margin-right: .5rem;
}

.active-modal {
    opacity: 1;
    visibility: visible;
}

ul {
    margin-top: 0.75rem;
}
li {
    display: flex;
    align-items: center;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
}

