@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #1f2833;
    --color-bg-variant: #2c2c6c;
    --color-primary: #00d4ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);
    --color-other: #0f243d;

    --color-bg2: #1f2833;
    --color-bg2-variant: #0b0c10;
    --color-primary2: #66FCF1;
    --color-primary2-variant: #45a29e;
    --color-light-variant: #c5c6c7;

    --transition: all 400ms ease;

    --container-width-l: 75%;
    --container-width-m: 86%;
    --container-width-s: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
}

body {
    background-color: #0b0c10;
        opacity: 1;
        background-image: radial-gradient(#1f2833 0.8500000000000001px, #0b0c10 0.8500000000000001px);
        background-size: 17px 17px;
}

/* other styles */

.container {
    width: var(--container-width-l);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2,
section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.35rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    height: 40px;
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-m);
    }

    section {
        margin-top: 6rem;
    }
}

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-s);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}

ul {
    list-style: none;
    display: inline;
}

li {
    display: inline;
}